import React from "react";
import SEO from "../../components/seo";
import { graphql, StaticQuery } from "gatsby";
import Trabajo from "../../components/trabajo";

export default () => (
  <StaticQuery
    query={graphql`
      {
        altaUach: file(relativePath: { eq: "altauach.png" }) {
          childImageSharp {
            fluid(maxWidth: 750) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        pokemonMastersDB: file(relativePath: { eq: "pokemonmastersdb.png" }) {
          childImageSharp {
            fluid(maxWidth: 750) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        cuidoMiMascota: file(relativePath: { eq: "gaap.png" }) {
          childImageSharp {
            fluid(maxWidth: 750) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        rentalValdiviaWeb: file(
          relativePath: { eq: "rentalvaldivia_web.png" }
        ) {
          childImageSharp {
            fluid(maxWidth: 750) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        rentalValdiviaApp: file(
          relativePath: { eq: "rentalvaldivia_app.png" }
        ) {
          childImageSharp {
            fluid(maxWidth: 750) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        caboBlanco: file(relativePath: { eq: "caboblanco.png" }) {
          childImageSharp {
            fluid(maxWidth: 750) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        obispadoValdivia: file(relativePath: { eq: "obispadovaldivia.png" }) {
          childImageSharp {
            fluid(maxWidth: 750) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        nanduSur: file(relativePath: { eq: "nandudelsur.png" }) {
          childImageSharp {
            fluid(maxWidth: 750) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        gestionAmbiental: file(relativePath: { eq: "gestionambiental.png" }) {
          childImageSharp {
            fluid(maxWidth: 750) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        ambiente: file(relativePath: { eq: "ambiente.png" }) {
          childImageSharp {
            fluid(maxWidth: 750) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        ceaChile: file(relativePath: { eq: "ceachile.png" }) {
          childImageSharp {
            fluid(maxWidth: 750) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        littleCinnamon: file(relativePath: { eq: "littlecinnamon.png" }) {
          childImageSharp {
            fluid(maxWidth: 750) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        immerse: file(relativePath: { eq: "immerse.png" }) {
          childImageSharp {
            fluid(maxWidth: 750) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        dentro: file(relativePath: { eq: "dentro.png" }) {
          childImageSharp {
            fluid(maxWidth: 750) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        demonRecruiter: file(relativePath: { eq: "demonrecruiter.png" }) {
          childImageSharp {
            fluid(maxWidth: 750) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    `}
    render={data => (
      <section className="hero is-fullheight">
        <SEO title="Trabajos" keywords={[`trabajos`, `juan latorre`, `blog`]} />
        <div className="hero-body">
          <div className="container has-text-centered">
            <h1 className="title is-size-1">Mis últimos trabajos</h1>
            <div className="is-divider" />
            <div className="columns is-multiline">
              <Trabajo
                title="Alta Uach App"
                image={data.altaUach}
                description="Offline mobile app, api and web app for tests and data collection..."
                uri="https://github.com/juanlatorre/alta-uach"
                filters={["diseno-web", "movil"]}
              />

              <Trabajo
                title="Pokemon Masters DB"
                image={data.pokemonMastersDB}
                description="The Pokemon Masters unofficial Wiki/Database website/app with information on Sync Pairs, Trainers, Pokemon, Items and much more..."
                uri="https://github.com/PokemonMastersDB"
                filters={["diseno-web", "movil"]}
              />

              <Trabajo
                title="Cuido Mi Mascota"
                image={data.cuidoMiMascota}
                description="Programa de educación en tenencia responsable de mascotas..."
                uri="https://cuidomimascota.cl"
                filters={["diseno-web", "administracion"]}
              />

              <Trabajo
                title="Rental Valdivia App"
                image={data.rentalValdiviaApp}
                description="En el catálogo digital de Rental Valdivia, podrás ver nuestra lista de productos y realizar cotizaciones desde la misma aplicación...."
                uri="https://play.google.com/store/apps/details?id=com.juanlatorre.rentalvaldivia"
                filters={["movil", "administracion"]}
              />

              <Trabajo
                title="Rental Valdivia Web"
                image={data.rentalValdiviaWeb}
                description="Somos una empresa orientada a brindar un servicio de calidad a diferentes sectores industriales...."
                uri="https://rentalvaldivia.com"
                filters={["diseno-web", "administracion"]}
              />

              <Trabajo
                title="Cabo Blanco Cocina & Taller"
                image={data.caboBlanco}
                description="Un encuentro con la gastronomía Valdiviana, auténtica cocina local con fusión de sabores y técnicas, cocina de mercado y temporada, que une..."
                uri="https://caboblanco.cl"
                filters={["diseno-web", "administracion"]}
              />

              <Trabajo
                title="Obispado de Valdivia"
                image={data.obispadoValdivia}
                description="La diócesis de Valdivia fue un proyecto cuyos orígenes datan del siglo XIII, pero que se hace realidad el 8 de julio de 1944, mediante la bula..."
                uri="http://obispadodevaldivia.cl/"
                filters={["diseno-web"]}
              />

              <Trabajo
                title="Ñandú del Sur"
                image={data.nanduSur}
                description="Es el ave de mayor tamaño en Chile. Resalta su cuello y piernas largas (largo total de 90 a 100 cm). Su peso varía entre 15 a 25 kilos entre..."
                uri="http://ceachile.cl/nandu/"
                filters={["diseno-web", "administracion"]}
              />

              <Trabajo
                title="Gestión Ambiental"
                image={data.gestionAmbiental}
                description="Gestión Ambiental es una revista científica cuyo objetivo es difundir trabajos en las diferentes herramientas de la gestión ambiental: historia..."
                uri="http://ceachile.cl/revista/"
                filters={["diseno-web", "administracion"]}
              />

              <Trabajo
                title="Ambiente"
                image={data.ambiente}
                description="Revista de divulgación de la Escuela de Ciencias Ambientales, Facultad de Recursos Naturales, Universidad Católica de Temuco..."
                uri="http://tepu.cl/"
                filters={["diseno-web", "administracion"]}
              />

              <Trabajo
                title="Centro de Estudios Agrarios y Ambientales"
                image={data.ceaChile}
                description="El CENTRO DE ESTUDIOS AGRARIOS & AMBIENTALES (CEA) es una Corporación de derecho privado sin fines de lucro, fundada en marzo de..."
                uri="http://ceachile.cl/"
                filters={["administracion"]}
              />

              <Trabajo
                title="Little Cinnamon"
                image={data.littleCinnamon}
                description="As an unknown creature you start your journey on a ugly, depriment and uncomfortable place. You need to get out of there but controls are clumsy, run away..."
                uri="https://globalgamejam.org/2019/games/little-cinnamon"
                filters={["videojuego"]}
              />

              <Trabajo
                title="Immerse"
                image={data.immerse}
                description="It's a survival 2D video game. It's about rescue a scuba diver who is drowning in the deep sea..."
                uri="https://globalgamejam.org/2017/games/immerse"
                filters={["videojuego"]}
              />

              <Trabajo
                title="Dentro"
                image={data.dentro}
                description="Vivir o morir..."
                uri="https://globalgamejam.org/2015/games/dentro"
                filters={["videojuego"]}
              />

              <Trabajo
                title="Demon Recruiter"
                image={data.demonRecruiter}
                description="Eres un esbirro menor del infierno enviado por el mismo Satan para reclutar nuevos soldados demoniacos para la batalla del juicio final. Eres enviado a matar..."
                uri="https://globalgamejam.org/2018/games/demon-recruiter"
                filters={["videojuego"]}
              />
            </div>
          </div>
        </div>
      </section>
    )}
  />
);
